import React from 'react';
import '../../App.css';
import Sidebar from "./sidebarBlog.js";
import '../../stylesheets/sidebar.css'
import { isBrowser } from 'react-device-detect';
import { BrowserRouter, Route } from "react-router-dom";
import AllPosts from "./AllPosts.js";
import OnePost from "./OnePost.js";

function SidebarIfBrowser() {

  if (isBrowser) {
    return <Sidebar />
  }
  else {
    return null
  }
}


function App() {
  return (
    <div className="App" >
      <header className="App-header" width="100%" >
        <div id="home" width="100%">
          <BrowserRouter id="blog">
              
              <div>
                <Route component={AllPosts} exact path="/blog/"  />
                <Route component={OnePost} exact path="/blog/:slug" />
              </div>
              
          </BrowserRouter>

          <SidebarIfBrowser />

        </div>
      </header>
    </div>
  );
}

export default App;
